// @require '../../style/reset.scss'

import React from 'react';
import ReactDom from 'react-dom';
import Datepicker from '../index';

const Demo = function () {
  return (
    <div>
      <h2>Datepicker Disabled</h2>
      <Datepicker
        selected={new Date()}
        placeholder={'点击选择日期'}
        showClean
        showIcon
        disabled
      />
    </div>
  );
};

ReactDom.render(
  <Demo />,
  document.getElementById('demo15')
);
